'use client';

import { useEffect, useState } from 'react';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
} from 'recharts';
import { API_URL } from '../api/apiURL';

export default function ReportsThisWeek() {
  const [reportData, setReportData] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(`${API_URL}/admin/reports-this-week`);
        const json = await res.json();
        if (json.success) setReportData(json.data);
      } finally {
        setLoading(false);
      }
    })();
  }, []);

  return (
    <div className="p-4 sm:p-6 max-w-6xl mx-auto">

      {/* CARD */}
      <div
        className="rounded-[24px] p-8"
        style={{
          background: "#FFFFFF",
          border: "1px solid #D6D6D6",
          boxShadow: "0px 6px 18px rgba(0,0,0,0.12)",
        }}
      >

        {/* Title + Legend */}
        <div className="flex items-center justify-between mb-6">
          <h3 className="text-[26px] font-semibold text-gray-900">
            Reports This Week
          </h3>

          <div className="flex items-center gap-7 text-[16px] font-medium text-gray-800">

            {/* Fake */}
            <div className="flex items-center gap-2">
              <span
                style={{
                  width: "16px",
                  height: "16px",
                  borderRadius: "6px",
                  background: "#FF7C7C",
                  border: "2px solid #FF7C7C",
                }}
              />
              Fake
            </div>

            {/* Inappropriate */}
            <div className="flex items-center gap-2">
              <span
                style={{
                  width: "16px",
                  height: "16px",
                  borderRadius: "6px",
                  background: "#FFC400",
                  border: "2px solid #FFC400",
                }}
              />
              Inappropriate profile
            </div>

            {/* Spam */}
            <div className="flex items-center gap-2">
              <span
                style={{
                  width: "16px",
                  height: "16px",
                  borderRadius: "6px",
                  background: "#76D64C",
                  border: "2px solid #76D64C",
                }}
              />
              Spam
            </div>

            {/* Harassment */}
            <div className="flex items-center gap-2">
              <span
                style={{
                  width: "16px",
                  height: "16px",
                  borderRadius: "6px",
                  background: "#47D0FF",
                  border: "2px solid #47D0FF",
                }}
              />
              Harassment
            </div>

          </div>
        </div>

        {/* CHART */}
        {loading ? (
          <p>Loading...</p>
        ) : (
          <div className="w-full h-[400px]">
            <ResponsiveContainer width="100%" height="100%">
              <BarChart
                data={reportData}
                margin={{ top: 10, right: 20, left: 0, bottom: 5 }}
                barCategoryGap={28}
              >
                {/* GRID EXACT LIKE SCREENSHOT */}
                <CartesianGrid
                  stroke="#D8D8D8"
                  strokeWidth={1.2}
                  vertical={false}
                />

                <XAxis
                  dataKey="day"
                  tick={{ fill: "#4B4B4B", fontSize: 16 }}
                />

                <YAxis
                  domain={[0, 70]}
                  ticks={[0, 10, 20, 30, 40, 50, 60, 70]}
                  tickFormatter={(v) => `${v} %`}
                  tick={{ fill: "#4B4B4B", fontSize: 16 }}
                />

                <Tooltip
                  formatter={(v) => `${v}%`}
                  contentStyle={{
                    borderRadius: "12px",
                    border: "1px solid #ccc",
                    background: "#ffffff",
                    fontSize: "14px",
                    boxShadow: "0 2px 10px rgba(0,0,0,0.18)",
                  }}
                />

                {/* STACKED BARS EXACT SAME COLORS */}
                <Bar dataKey="fake" stackId="a" fill="#FF7C7C" radius={[5, 5, 0, 0]} />
                <Bar dataKey="inappropriate" stackId="a" fill="#FFC400" />
                <Bar dataKey="spam" stackId="a" fill="#76D64C" />
                <Bar dataKey="harassment" stackId="a" fill="#47D0FF" />

              </BarChart>
            </ResponsiveContainer>
          </div>
        )}
      </div>
    </div>
  );
}
