// src/component/analytics/ProfileMatch.jsx
"use client";

import dynamic from "next/dynamic";
import Highcharts from "highcharts";
import React, { useEffect, useState } from "react";
import { API_URL } from "../api/apiURL";

// Reference screenshots (local paths you uploaded)
// These local paths will be transformed to URLs by your toolchain if needed.
const REF_PIE = "/mnt/data/Screenshot 2025-11-25 151215.png";
const REF_MATCH = "/mnt/data/Screenshot 2025-11-25 151230.png";
const REF_FULL = "/mnt/data/Screenshot 2025-11-25 151241.png";

// dynamic import to avoid SSR issues
const HighchartsReact = dynamic(() => import("highcharts-react-official"), {
  ssr: false,
});

export default function ProfileMatch() {
  const months = [
    "Jan","Feb","Mar","Apr","May","Jun",
    "Jul","Aug","Sep","Oct","Nov","Dec"
  ];

  const [pieData, setPieData] = useState([]);
  const [totalUsers, setTotalUsers] = useState(new Array(12).fill(0));
  const [matches, setMatches] = useState(new Array(12).fill(0));

  // --- Load Profile Overview ---
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(API_URL + "/admin/overview");
        const api = await res.json();
        if (!api.success) return;

        const order = ["Completed", "Low", "Incomplete", "Moderate"];
        const colorMap = {
          Completed: "#0c7c1b",
          Low: "#f6931d",
          Incomplete: "#e21d1d",
          Moderate: "#f6c927",
        };

        setPieData(
          order.map((label) => {
            const found = api.data.find((x) => x.name === label);
            return { name: label, y: found ? found.value : 0, color: colorMap[label] };
          })
        );
      } catch (err) {
        console.error("Profile overview fetch error:", err);
      }
    })();
  }, []);

  // --- Load Matches per Month ---
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(API_URL + "/admin/matches-per-month");
        const api = await res.json();
        if (!api.success) return;

        setTotalUsers(months.map((m) => api.data.find((x) => x.month === m)?.totalUsers || 0));
        setMatches(months.map((m) => api.data.find((x) => x.month === m)?.matches || 0));
      } catch (err) {
        console.error("matches-per-month fetch error:", err);
      }
    })();
  }, []);

  // --- PIE options (fake-3D via border + shadow + startAngle) ---
  const pieOptions = {
    chart: { type: "pie", backgroundColor: "transparent", height: 340 },
    title: { text: "" },
    legend: { enabled: false },
    plotOptions: {
      pie: {
        borderWidth: 8,
        borderColor: "#ffffff",
        dataLabels: { enabled: false },
        shadow: {
          color: "rgba(0,0,0,0.28)",
          offsetX: 0,
          offsetY: 12,
          width: 22,
          opacity: 0.8,
        },
        slicedOffset: 8,
        startAngle: -30,
      },
    },
    tooltip: {
      useHTML: true,
      formatter() {
        const total = this.series.data.reduce((s, p) => s + (p.y || 0), 0) || 1;
        const pct = ((this.point.y / total) * 100).toFixed(1);
        return `<div style="font-weight:700;color:#222">${this.point.name}</div>
                <div style="margin-top:4px">${this.point.y} Users &nbsp; (<span style="font-weight:700">${pct}%</span>)</div>`;
      },
      backgroundColor: "rgba(255,255,255,0.98)",
      borderColor: "#e6e6e6",
      style: { color: "#222" },
    },
    series: [{ type: "pie", data: pieData }],
    credits: { enabled: false },
  };

  // --- MATCH options (rounded golden bars + red line) ---
  const matchOptions = {
    chart: { backgroundColor: "transparent", height: 420 },
    title: { text: "" },
    legend: { enabled: false },
    xAxis: {
      categories: months,
      labels: { style: { fontSize: "14px", fontWeight: 600, color: "#444" } },
      lineColor: "#eee",
    },
    yAxis: {
      min: 0,
      max: 35,
      tickInterval: 5,
      gridLineColor: "#e6e6e6",
      labels: {
        formatter() { return this.value + " M"; },
        style: { fontWeight: 600, color: "#666" },
      },
      title: { text: "" },
    },
    plotOptions: {
      column: {
        borderRadius: 24,
        pointPadding: 0.12,
        borderWidth: 0,
        grouping: true,
        shadow: { color: "rgba(0,0,0,0.28)", offsetX: 0, offsetY: 5, width: 12 },
      },
      series: {
        states: { hover: { enabled: true, brightness: 0.03 } },
      },
    },
    tooltip: {
      shared: true,
      useHTML: true,
      formatter() {
        let html = `<div style="font-weight:700;margin-bottom:6px">${this.x}</div>`;
        this.points.forEach((pt) => {
          html += `<div style="margin:4px 0">
                    <span style="display:inline-block;width:10px;height:10px;background:${pt.color};border-radius:2px;margin-right:8px;vertical-align:middle"></span>
                    <strong style="font-weight:600">${pt.series.name}:</strong>
                    &nbsp; ${pt.y}
                   </div>`;
        });
        return html;
      },
      backgroundColor: "rgba(255,255,255,0.98)",
      borderColor: "#e6e6e6",
      style: { color: "#222" },
    },
    series: [
      {
        type: "column",
        name: "Total no. of Users",
        color: {
          linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
          stops: [
            [0, "#f7d34a"],
            [1, "#d7a304"],
          ],
        },
        data: totalUsers,
      },
      {
        type: "line",
        name: "No. Of Matches",
        color: "#e52b2b",
        lineWidth: 4,
        marker: {
          enabled: true,
          radius: 7,
          fillColor: "#ffffff",
          lineColor: "#e52b2b",
          lineWidth: 2,
        },
        data: matches,
      },
    ],
    credits: { enabled: false },
  };

  // legend icon style per your confirmation:
  // size: 16px, border: 2.5px, radius: 4px
  const legendBoxStyle = {
    width: 16,
    height: 16,
    borderRadius: 4,
    background: "#fff",
    boxSizing: "border-box",
  };

  return (
    <div className="w-full p-6">
      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6 items-start">
        {/* LEFT: Profile Overview */}
        <div className="bg-white rounded-2xl shadow-xl p-6 relative" style={{ border: "2px solid rgba(0,0,0,0.08)", minHeight: 360 }}>
          {/* Title left */}
          <div className="flex items-start justify-between">
            <h2 style={{ fontSize: 26, fontWeight: 700, color: "#222" }}>Profile Overview</h2>
            <div style={{ width: 1 }} />
          </div>

          {/* Custom vertical legend (top-right) */}
          <div style={{ position: "absolute", right: 28, top: 22, display: "flex", flexDirection: "column", gap: 12, fontSize: 17, fontWeight: 600, color: "#333" }}>
            <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
              <div style={{ ...legendBoxStyle, border: "2.5px solid #0c7c1b" }} />
              <span>Completed</span>
            </div>

            <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
              <div style={{ ...legendBoxStyle, border: "2.5px solid #f6931d" }} />
              <span>Low</span>
            </div>

            <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
              <div style={{ ...legendBoxStyle, border: "2.5px solid #e21d1d" }} />
              <span>Incomplete</span>
            </div>

            <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
              <div style={{ ...legendBoxStyle, border: "2.5px solid #f6c927" }} />
              <span>Moderate</span>
            </div>
          </div>

          {/* Pie chart */}
          <div style={{ marginTop: 36 }}>
            <HighchartsReact highcharts={Highcharts} options={pieOptions} />
          </div>
        </div>

        {/* RIGHT: Matches Per Month */}
        <div className="bg-white rounded-2xl shadow-xl p-6 relative" style={{ border: "2px solid rgba(0,0,0,0.08)", minHeight: 520 }}>
          {/* Header with custom horizontal legend top-right */}
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start" }}>
            <h2 style={{ fontSize: 26, fontWeight: 700, color: "#222" }}>Matches Per Month</h2>

            <div style={{ display: "flex", gap: 48, alignItems: "center", fontSize: 16, fontWeight: 600, color: "#333" }}>
              <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
                <div style={{ ...legendBoxStyle, border: "2.5px solid #d7a304" }} />
                <span>Total no. of Users</span>
              </div>

              <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
                <div style={{ ...legendBoxStyle, border: "2.5px solid #e52b2b" }} />
                <span>No. Of Matches</span>
              </div>
            </div>
          </div>

          {/* Chart */}
          <div style={{ marginTop: 28 }}>
            <HighchartsReact highcharts={Highcharts} options={matchOptions} />
          </div>
        </div>
      </div>
    </div>
  );
}
