import React, { useEffect, useState } from "react";
// FIX: Highcharts and HighchartsReact are loaded globally in the environment,
// so we remove the failed module imports and assume they are accessible.

// --- MOCK DATA TO MATCH SCREENSHOTS ---
// 1. Search To Match (Funnel)
const MOCK_FUNNEL_DATA = [
  { stage: "User Searched", value: 919980 }, // 919.98 K
  { stage: "Profile Viewed", value: 876700 }, // 876.70 K
  { stage: "Expressed Interest", value: 597120 }, // 597.12 K
  { stage: "Matched Successfully", value: 299220 }, // 299.22 K
];

// 2. Running User Trend (Line Chart)
// These small values are used to represent Millions (M) in the chart config.
const MOCK_TREND_DATA = [
  { date: "Sun", newUsers: 24, returning: 10 },   // Su: 24M, 10M
  { date: "Mon", newUsers: 22.5, returning: 15 }, // Mo: 22.5M, 15M
  { date: "Tue", newUsers: 17, returning: 13 },   // Tu: 17M, 13M
  { date: "Wed", newUsers: 18, returning: 13.5 }, // We: 18M, 13.5M
  { date: "Thu", newUsers: 21.5, returning: 14 }, // Th: 21.5M, 14M
  { date: "Fri", newUsers: 26, returning: 20 },   // Fr: 26M, 20M
  { date: "Sat", newUsers: 28, returning: 25 },   // Sa: 28M, 25M
];

// --- UTILITY FUNCTIONS ---

// format thousands (1 -> "1.00 K")
const formatK = (num) => (num / 1000).toFixed(2) + " K";

// desired weekday order and mapping to display labels
const WEEK_ORDER = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
const WEEK_LABELS = { Sun: "Su", Mon: "Mo", Tue: "Tu", Wed: "We", Thu: "Th", Fri: "Fr", Sat: "Sa" };

// helper to normalize incoming day strings to standard three-letter names
const normalizeDay = (s) => {
  if (!s) return s;
  const map = {
    sun: "Sun", mon: "Mon", tue: "Tue", wed: "Wed", thu: "Thu", fri: "Fri", sat: "Sat",
    sunday: "Sun", monday: "Mon", tuesday: "Tue", wednesday: "Wed", thursday: "Thu", friday: "Fri", saturday: "Sat",
    Su: "Sun", Mo: "Mon", Tu: "Tue", We: "Wed", Th: "Thu", Fr: "Fri", Sa: "Sat",
  };
  const key = String(s).trim().toLowerCase();
  // Simplified normalization for mock data
  return map[key] || (s.length >= 3 ? (s[0].toUpperCase() + s.slice(1,3).toLowerCase()) : s);
};

// Check if Highcharts objects are available globally
const Highcharts = window.Highcharts;
const HighchartsReact = window.HighchartsReact;


export default function App() {
  const [trendData, setTrendData] = useState([]);   // array of { day: "Sun", new: number, returning: number }
  const [funnelData, setFunnelData] = useState([]); // array from /admin/search-to-match
  const [isChartsReady, setIsChartsReady] = useState(!!Highcharts && !!HighchartsReact);


  // Load data (mocked)
  useEffect(() => {
    // Check if charts libs are available after initial render
    if (!isChartsReady && window.Highcharts && window.HighchartsReact) {
        setIsChartsReady(true);
    }
    
    // Simulate API fetch delay for trend data
    setTimeout(() => {
      const byDay = {};
      MOCK_TREND_DATA.forEach((d) => {
        const nd = normalizeDay(d.date);
        byDay[nd] = byDay[nd] || { new: 0, returning: 0 };
        byDay[nd].new = (byDay[nd].new || 0) + (Number(d.newUsers) || 0);
        byDay[nd].returning = (byDay[nd].returning || 0) + (Number(d.returning) || 0);
      });

      const ordered = WEEK_ORDER.map((w) => {
        const val = byDay[w] || { new: 0, returning: 0 };
        return { day: w, new: Number(val.new || 0), returning: Number(val.returning || 0) };
      });

      setTrendData(ordered);
    }, 500);

    // Simulate API fetch delay for funnel data
    setTimeout(() => {
      setFunnelData(MOCK_FUNNEL_DATA);
    }, 500); 
  }, [isChartsReady]);

  // --- HIGHCHARTS CONFIGURATION ---

  // Highcharts series data (using the small values that represent Millions)
  const seriesNew = trendData.map((d) => (Number(d.new) || 0));
  const seriesReturning = trendData.map((d) => (Number(d.returning) || 0));

  const chartOptions = {
    chart: {
      type: "line",
      height: 360,
      backgroundColor: "#ffffff",
      spacingTop: 10,
      spacingLeft: 50, 
    },

    // Title and inline legend on the same horizontal line (useHTML)
    title: {
      useHTML: true,
      text: `
        <div style="display:flex;justify-content:space-between;align-items:center;width:100%;padding-right:20px;">
          <div style="font-size:28px;font-weight:700;color:#111827;">Running User Trend</div>

          <div style="display:flex;gap:36px;align-items:center;font-size:16px;color:#6b7280; white-space: nowrap;">
            <div style="display:flex;align-items:center;gap:8px;">
              <span style="
                width:18px;height:18px;border-radius:50%;
                border:4px solid #facc15;background:#fff;display:inline-block;">
              </span>
              <span>New Registered Users</span>
            </div>

            <div style="display:flex;align-items:center;gap:8px;">
              <span style="
                width:18px;height:18px;border-radius:50%;
                border:4px solid #16a34a;background:#fff;display:inline-block;">
              </span>
              <span>Returning Users</span>
            </div>
          </div>
        </div>
      `,
      align: "left",
      x: 0,
      y: 15,
      style: {
        width: '100%',
      }
    },

    legend: { enabled: false },

    xAxis: {
      categories: trendData.map((d) => WEEK_LABELS[d.day] || d.day),
      lineWidth: 0,
      tickLength: 0,
      labels: { 
        style: { fontSize: "18px", fontWeight: 500, color: "#374151" } 
      },
    },

    yAxis: {
      title: null,
      gridLineColor: "#e5e7eb",
      // Ticks at 0, 5, 10, ... 30 M 
      tickPositions: [0, 5, 10, 15, 20, 25, 30], 
      labels: {
        // Format the small value (e.g., 20) to display as 20 M
        formatter() { return `${this.value} M`; }, 
        style: { fontSize: "18px", color: "#6b7280" },
      },
      min: 0,
      max: 30, // Ensure the max is 30M
    },

    tooltip: { 
      shared: true,
      formatter: function() {
        // Custom tooltip to show M scale
        let tooltip = `<span style="font-size: 10px">${this.x}</span><br/>`;
        this.points.forEach(function(point) {
          tooltip += `<span style="color:${point.series.color}">\u25CF</span> ${point.series.name}: <b>${point.y.toFixed(2)} M</b><br/>`;
        });
        return tooltip;
      }
    },

    plotOptions: {
      series: {
        lineWidth: 4,
        marker: {
          radius: 8, 
          lineWidth: 4, 
          lineColor: null, 
          fillColor: "#ffffff", // white center â€” double-ring effect
        },
        states: { hover: { enabled: true } },
        // Smooth line
        smoothing: true
      },
    },

    // Two series: New (yellow) and Returning (green)
    series: [
      {
        name: "New Registered Users",
        data: seriesNew,
        color: "#facc15",
        marker: { lineColor: "#facc15" },
      },
      {
        name: "Returning Users",
        data: seriesReturning,
        color: "#16a34a",
        marker: { lineColor: "#16a34a" },
      },
    ],

    credits: { enabled: false },
  };

  return (
    // Main container with dark background for contrast
    <div className="bg-gray-50 min-h-screen p-8 font-sans">
      <div className="pt-8 flex flex-col lg:flex-row gap-8 w-full max-w-7xl mx-auto">

        {/* LEFT: Search To Match */}
        <div className="bg-white rounded-2xl shadow-2xl border border-gray-200 p-8 lg:p-10 w-full lg:w-1/2">
          <h2 className="text-3xl font-extrabold mb-10 text-gray-900">Search To Match</h2>

          <div className="space-y-10">
            {funnelData.length === 0 ? (
              // Placeholder rows when no data (shows loading state)
              [1, 2, 3, 4].map((i) => (
                <div key={i} className="flex items-center justify-between opacity-50 animate-pulse">
                  <div className="w-[180px] text-[20px] font-medium text-gray-400">Loading Stage...</div>
                  <div className="flex-1 mx-4">
                    <div className="w-full h-[25px] bg-gray-200 rounded-full overflow-hidden">
                      <div className="h-full rounded-full w-[40%]" style={{ background: "linear-gradient(90deg,#93c5fd,#3b82f6)" }} />
                    </div>
                  </div>
                  <div className="w-[120px] text-right text-[20px] font-semibold text-gray-400">0.00 K</div>
                </div>
              ))
            ) : (
              // Render funnel data
              funnelData.map((item, idx) => {
                const maxVal = funnelData[0]?.value || 1; 
                const pct = (item.value / maxVal) * 100;

                return (
                  <div key={idx} className="flex items-center justify-between">
                    <div className="w-[200px] text-[20px] font-medium text-gray-700">
                      {item.stage}
                    </div>

                    <div className="flex-1 mx-4">
                      <div
                        className="w-full h-[30px] bg-gray-100 rounded-full overflow-hidden"
                        style={{ boxShadow: "inset 0 0 0 1px rgba(0,0,0,0.05)" }}
                      >
                        <div
                          className="h-full rounded-full transition-all duration-700 ease-out"
                          style={{
                            width: `${pct}%`,
                            // Gradient and shadow to match the screenshot's distinct blue bar style
                            background: "linear-gradient(90deg,#3A47FF,#001BFF)",
                            filter: "drop-shadow(0 6px 12px rgba(0,0,0,0.25))",
                          }}
                        />
                      </div>
                    </div>

                    <div className="w-[120px] text-right text-[20px] font-semibold text-gray-800">
                      {formatK(item.value)}
                    </div>
                  </div>
                );
              })
            )}
          </div>
        </div>

        {/* RIGHT: Running User Trend */}
        <div className="bg-white rounded-2xl shadow-2xl border border-gray-200 p-8 lg:p-10 w-full lg:w-1/2 flex flex-col justify-center">
          {/* Only render the chart if Highcharts components are available */}
          {isChartsReady && Highcharts && HighchartsReact ? (
            <HighchartsReact highcharts={Highcharts} options={chartOptions} />
          ) : (
             <div className="text-center p-16 text-gray-500 text-lg">
                Loading charting library...
             </div>
          )}
        </div>

      </div>
    </div>
  );
}